.eportfolio{
   width: 100%;
}
.main{
    height: 100vh;
}
.bg-cus-blue{
    background-color: #47B5FF;;
}

body{
    font-family: 'calendas_plusregular';
    background-color: #DFF6FF; font-family: system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
}

.web-prev{
    overflow: hidden;
}

.right-container{
    position: absolute;
    height: 100vh;
    width: 80vh;
    background-color: #fff;
    border-bottom-left-radius: 100%;
    z-index: -100;
    right: 0px;

}

header{
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 90%;
}

.name{
    color: #1363DF;
}


.logo p{
    font-size: 30px;
    text-decoration: none;
}

.navList{
    color: rgb(32, 32, 32);
    text-decoration: none;
}

.link{
    text-decoration: none;

}

.logo p > span{
    color: #1363DF;
    text-decoration: none;
}

header ul li {
    list-style: none;
    font-size: 30px;
    display: inline-block;
    margin: 10px 10px;
    
}
.nav-item{
    cursor: pointer;
}

.cards {
    
    transform-style: preserve-3d;
    transition: all 1s ease-in-out;
}
.cards:hover  {
transform: rotateY(180deg);
    
}
.cards .side {
position: absolute;
left: 0;
backface-visibility: hidden;
}

.cards .back {
position: relative;
left: 0;
transform: rotateY(180deg);
}

.hide{
    display: none;
}

.show{
    display: block;
}

main{
    font-size: 30px;
}

.paper-clip {
    width: 60px;
    height: 85px;
    background: url(https://lh5.googleusercontent.com/-_VvJpGXP9N0/UwdDY7l-CQI/AAAAAAAABTI/xXPDe4i5lbQ/s330/paper-clip.png) repeat scroll 0% 0% transparent;
    position: absolute;
    top: -14px;
    right: -13px;
}

main > .right > .bg-shape{
    border-radius: 20px;

    position: absolute;
    background-color: #1363DF;
    z-index: -100;
    height: 30%;
    width: 18%;
    transform: rotate(-50deg);
    animation: animeRotate 5s linear 0s infinite alternate;
}
